import React from 'react';
import { useStoreon } from 'storeon/react';
import OrderDetailsPersonalPageLayoutContainer from './OrderDetailsPersonalPageLayout/OrderDetailsPersonalPageLayoutContainer';

const OrderDetailsPersonalPage = ({
    context,
    loader
}) => {
    const { orderStore ,currency, balanceStore} = useStoreon('currency','orderStore', 'balanceStore');

    const {
        cabinet_site_menu,
        cabinet_menu,
        breadcrumbs,
        profile,
        shop,
    } = context;

    const {
        id,
        status,
        weight,
        services,
        discount,
        in_archive,
        total_cost,
        updated_at,
        order_cost,
        created_at,
        track_number,
        delivery_cost,
        specification,
        correspondence,
        dataOrderItems,
        payment_method,
        isLoadingOrder,
        delivery_method,
        delivery_address,
        deleteElementOrder,
    } = orderStore;
    
    const idOrder = id;
    const order_chat = correspondence?.order_chat;
    const order_items_chat = correspondence?.order_items_chat;
    const { user = {}, role } = profile;
    const { username } = user;
    const amountNotifications = profile?.notifications;
    const numberOrder = window.location.href.split('-').pop();
    const fullNumberOrder = window.location.href.split('/').pop();
    
    return (
        <OrderDetailsPersonalPageLayoutContainer
            currency={currency}
            balance={balanceStore.balance}
            amountNotifications={amountNotifications}
            cabinet_site_menu={cabinet_site_menu}
            isLoadingOrder={isLoadingOrder}
            cabinet_menu={cabinet_menu}
            breadcrumbs={breadcrumbs}
            is_has_shop={shop?.is_has_shop}
            create_shop={shop?.shop_link}
            username={username}
            shop={shop}
            role={role}

            idOrder={idOrder}
            status={status}
            weight={weight}
            services={services}
            discount={discount}
            in_archive={in_archive}
            total_cost={total_cost}
            updated_at={updated_at}
            order_cost={order_cost}
            created_at={created_at}
            track_number={track_number}
            delivery_cost={delivery_cost}
            specification={specification}
            // dataOrderItems={dataOrderItems}
            payment_method={payment_method}
            order_items_chat={order_items_chat}
            order_chat={order_chat}
            delivery_method={delivery_method}
            fullNumberOrder={fullNumberOrder}
            numberOrder={numberOrder}
            delivery_address={delivery_address}
            deleteElementOrder={deleteElementOrder}
        />
    )
}

export default (OrderDetailsPersonalPage);