import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useStoreon } from 'storeon/react';
import OrderDetailsPersonalPageLayout from './OrderDetailsPersonalPageLayout';

const OrderDetailsPersonalPageLayoutContainer = ({
  amountNotifications,
  cabinet_site_menu,
  isLoadingOrder,
  cabinet_menu,
  breadcrumbs,
  create_shop,
  is_has_shop,
  currency,
  username,
  shop,
  role,

  idOrder,
  status,
  weight,
  services,
  discount,
  in_archive,
  total_cost,
  updated_at,
  order_cost,
  created_at,
  delivery_cost,
  delivery_method,
  delivery_address,
  fullNumberOrder,
  numberOrder,
  payment_method,
  specification,
  track_number,
  // dataOrderItems,
  activeButtonSpecification,
}) => {

  const { dispatch, correspondence, orderStore,balanceStore } = useStoreon('correspondence', 'orderStore','balanceStore');
  const { orderStoreListItem } = useStoreon('orderStoreListItem');
  const { balance, total_orders_price_unpaid } = balanceStore;

  const dataOrderItems = orderStoreListItem.dataOrderItems;
  const navigate = useNavigate();
  const [ countProductInOrder, setCountProductInOrder ] = useState(0);
  const [discardProduct, setDiscardProduct] = useState(0)
  const [isLoaderSendMessage, setIsLoaderSendMessage] = useState(false)

  useEffect(()=>{
    let count = 0;
    let sum = 0;
    if(dataOrderItems.length && dataOrderItems[0]?.items){
      count = dataOrderItems.reduce((acc, cur)=>{
        acc += cur.items.length;
        cur.items.filter((item) => {
          if (item.status.id === 'canceled') sum = sum + 1
      })
        return acc
      },0);
    }else{
      count = dataOrderItems.length;
      dataOrderItems.filter((el, i) => {
        if (el.status.id === 'canceled') sum = sum + 1
      })
    }
      setDiscardProduct( sum );
      setCountProductInOrder(count);
  },[dataOrderItems, orderStore])


  const openModalPay = (NumberOrder, total_cost) => {
    const params = {
      order_id: NumberOrder,
      total_price: total_cost,
      total_orders_price_unpaid: total_orders_price_unpaid
    }
    dispatch('modalCheckPayment', params)
  };


  const heandlerClickInfo = (status) => {

    dispatch('modalShowInfoOrder', { status: status })
    // dispatch('correspondence')
  }

  const handlerSpecification = (idOrder) => {
    dispatch('getSpecification',{idOrder: idOrder})
  }

  const deleteElementOrder = (id) => {
    const params = {
      id_goods: id,
      redirectTo: (path) => {
        const timerTimeout = setTimeout(() => {
            navigate(path);
            return () => clearTimeout(timerTimeout);
        }, 500)
    }    
    }
    dispatch('removeItemFromOrder', params)
    console.log('click')
  }


  const sendCommentFromTextField = (order_id) => {
    console.log('send message', order_id)
  };

  const clickOpenCommit = (values, setFieldValue) => {
    const params = {
      idProduct: values.idProduct
    }
    dispatch('changeStateIsnewMessage', params)
    setFieldValue('upDownBtn', !values.upDownBtn)
  }

  const openModalImage = (image, url = null) => {
    dispatch('openModalPhoto', {
      controll: true,
      image: image,
      urlProduct: url
    })
  };

  const openModalVideo = (video, preview, url = null) => {
    dispatch('openModalVideo', {
      video: video,
      preview: preview,
      urlProduct: null
    })
  };


  const sendMessage = (values, { setFieldValue }) => {
    const params = {
      ...values,
      idProduct: values?.orderChat ? values.orderChat : values.idProduct,
      setFieldValue: setFieldValue,
      setIsLoaderSendMessage: setIsLoaderSendMessage,
    }
    if (values?.orderChat) {
      dispatch('sendMessageChatProduct', params)
    } else {
      dispatch('sendMessageProduct', params)
    }
  }
  return (
    <OrderDetailsPersonalPageLayout
      isLoaderSendMessage={isLoaderSendMessage}
      amountNotifications={amountNotifications}
      countProductInOrder={countProductInOrder}
      cabinet_site_menu={cabinet_site_menu}
      isLoadingOrder={isLoadingOrder}
      cabinet_menu={cabinet_menu}
      breadcrumbs={breadcrumbs}
      create_shop={create_shop}
      is_has_shop={is_has_shop}
      currency={currency}
      username={username}
      balance={balance}
      shop={shop}
      role={role}
      discardProduct={discardProduct}
      idOrder={idOrder}
      status={status}
      weight={weight}
      services={services}
      discount={discount}
      in_archive={in_archive}
      total_cost={total_cost}
      updated_at={updated_at}
      order_cost={order_cost}
      created_at={created_at}
      numberOrder={numberOrder}
      order_items_chat={correspondence?.order_items_chat}
      order_chat={correspondence?.order_chat}
      delivery_cost={delivery_cost}
      delivery_method={delivery_method}
      fullNumberOrder={fullNumberOrder}
      delivery_address={delivery_address}
      payment_method={payment_method}
      specification={specification}
      track_number={track_number}
      dataOrderItems={dataOrderItems}

      openModalPay={openModalPay}
      heandlerClickInfo={heandlerClickInfo}
      handlerSpecification={handlerSpecification}
      activeButtonSpecification={activeButtonSpecification}
      deleteElementOrder={deleteElementOrder}
      sendCommentFromTextField={sendCommentFromTextField}
      clickOpenCommit={clickOpenCommit}
      openModalImage={openModalImage}
      openModalVideo={openModalVideo}
      sendMessage={sendMessage}
    />
  )
}

export default (OrderDetailsPersonalPageLayoutContainer);