import React, { memo, useEffect, useState } from 'react';
import dayjs from '../../../helpers/dayjs';
import Title from '../../../Views/Title';
import api from '../../../api/api';
import SubTitle from '../../../Views/ElementPage/SubTitle/SubTitle';
import ContainerTitle from '../../../Views/ElementPage/ContainerTitle/ContainerTitle';
import TrackDetails from './TrackDetails';
import OrderBaseDetails from '../OrderDetailsPersonalPage/OrderDetailsPersonalPageLayout/DetailsOrders/OrderBaseDetails';
import TextUnderTitle from '../../../Views/TextUnderTitle';
import { ROLE } from '../../../const';
import ListTable from '../../../Views/ElementPage/ListTable/ListTable';
import SectionWrapper from '../../../Views/ElementPage/SectionWrapper/SectionWrapper';
import LeftSideCol from '../../../Views/ElementPage/LeftSideCol/LeftSideCol';
import BlockSpinner from '../../../Views/SpinnerWrapper';
import Card from '../../../Views/ElementPage/CardOrder/Card';
import WrapperWhoosaleCard from '../../../Views/ElementPage/CardOrder/WrapperWhoosaleCard';
import RightSideCol from '../../../Views/ElementPage/RightSideCol/RightSideCol';
import Chat from '../../../Views/ElementPage/Chat/Chat';
import { useStoreon } from 'storeon/react';

const DetailsOrders = ({
    role,
    idOrder,
    status,
    weight,
    services,
    discount,
    currency,
    in_archive,
    total_cost,
    updated_at,
    order_cost,
    created_at,
    numberOrder,
    delivery_cost,
    isLoadingOrder,
    delivery_method,
    delivery_address,
    fullNumberOrder,
    payment_method,
    specification,
    track_number,
    // dataOrderItems,
    order_items_chat,
    countProductInOrder,
    order_chat,
    // isLoadingCancelItems,

    openModalPay,
    heandlerClickInfo,
    handlerSpecification,
    activeButtonSpecification,

    deleteElementOrder,
    sendCommentFromTextField,
    clickOpenCommit,
    openModalImage,
    openModalVideo,
    sendMessage,
    discardProduct,
    isLoaderSendMessage,
}) => {
    const { orderStoreListItem, statusChat, dispatch } = useStoreon('orderStoreListItem', 'statusChat');
    const dataOrderItems = orderStoreListItem.dataOrderItems;
    const isLoadingCancelItems = orderStoreListItem.isLoadingCancelItems;
    const handleStatusChat = (status) => {
        console.log('click websocket', status);
        //dispatch('correspondence', {isOpenWebsocket: status})
    }

    return (
        <React.Fragment>
            <ContainerTitle
                variant={'create-order__title-container'}
            >
                <Title variant="cabinet_orders_details__title" type={'h1'}>
                    Заказ № {fullNumberOrder}
                </Title>

                {
                    created_at?
                        <SubTitle
                            variant={'create-order__subtitle'}
                        >
                            от {dayjs(api.language, created_at).format('DD.MM.YYYY')}
                        </SubTitle>
                        : null
                }
            </ContainerTitle>

            {
                track_number ? (
                    <TrackDetails nubmerTrack={track_number} />
                ) : null
            }
            {/*
//?! шапка с информацией о заказе
*/}
{
    total_cost?
            <OrderBaseDetails
                role={role}
                idOrder={idOrder}
                status={status}
                weight={weight}
                services={services}
                discount={discount}
                in_archive={in_archive}
                total_cost={total_cost}
                updated_at={updated_at}
                order_cost={order_cost}
                created_at={created_at}
                currency={currency}
                delivery_cost={delivery_cost}
                delivery_method={delivery_method}
                numberOrder={numberOrder}
                delivery_address={delivery_address}
                payment_method={payment_method}
                specification={specification}
                track_number={track_number}

                openModalPay={openModalPay}
                heandlerClickInfo={heandlerClickInfo}
            />
            : <BlockSpinner.SpinnerWrapperSpinnerCenterMargin>
                        <BlockSpinner.Spinner sizeHeight='30' sizeWidth='30' />
                    </BlockSpinner.SpinnerWrapperSpinnerCenterMargin>
}
            {
                role === ROLE.WHOLESALE ? (
                    <>
                        <TextUnderTitle variant={'text-content__order-info'}>
                            Возможна частичная отправка товара (если выкуплены не все заказанные единицы). В случае необходимости отправить выкупленный товар с заказа напишите об этом в "Чате по заказу"
                        </TextUnderTitle>
                        <TextUnderTitle variant={'text-content__order-info'}>
                            В случае необходимости отмены товара (ов) в заказе и возврата денег на баланс, нажмите кнопку "ОТМЕНИТЬ" рядом с товаром. Если эта кнопка отсутствует, значит, при отмене данного товара сумма заказа станет менее 5000 PLN, либо нарушается условие обязательного количества единиц с бренда. В этой ситуации необходимо сначала добавить в заказ другие товары, а затем отменить те, которые более не нуждаются в выкупе.
                        </TextUnderTitle>
                        <TextUnderTitle variant={'text-content__order-info'}>
                            Также обращаем внимание, что подбор размеров и проверка на брак не осуществляются.
                        </TextUnderTitle>
                    </>
                ) : null
            }
            {/*
        //?! количество товаров в заказе
        */}
            <ListTable
                count={countProductInOrder ?? 0}  //{role !== ROLE.WHOLESALE ? orderItems.length : orderItemLength}
                handlerSpecification={handlerSpecification}
                activeButtonSpecification={activeButtonSpecification}
                idOrder={numberOrder}
                discardProduct={discardProduct}
            />
            {/*
        //?! Список товаров
        */}
                    <SectionWrapper>
                        <LeftSideCol>
            {
                dataOrderItems.length ?
                                role !== ROLE.WHOLESALE ? (
                                    dataOrderItems.map((el, i) => {
                                        let contextChats = [];
                                        if (order_items_chat?.length) {
                                            contextChats = order_items_chat.filter(message => message.item_id === el.id)
                                        }
                                        return (
                                            <Card
                                                key={`el-${el.id}`}
                                                title={el.title}
                                                size={el.size}
                                                role={role}
                                                color={el.color}
                                                status={el.status}
                                                prices={el.prices}
                                                order={el.order}
                                                brand={el.brand}
                                                change_agreement={el.change_agreement}
                                                image={el.image}
                                                url={el.product_url}
                                                can_cancel={el.can_cancel}
                                                redeemed={el.redeemed}
                                                sended={el.sended}
                                                currency={currency}
                                                contextChats={contextChats[0]}
                                                isLoadingCancelItems={isLoadingCancelItems}
                                                product_url={el.product_url}
                                                deleteElementOrder={deleteElementOrder}
                                                sendCommentFromTextField={sendCommentFromTextField}
                                                clickOpenCommit={clickOpenCommit}
                                                openModalImage={openModalImage}
                                                sendMessage={sendMessage}
                                                statusChat={statusChat}
                                                handleStatusChat={handleStatusChat}
                                                isLoaderSendMessage={isLoaderSendMessage}
                                                {...el}
                                            />
                                        );
                                    })
                                ) : (
                                    <React.Fragment>
                                        {
                                            dataOrderItems.map((el, i) => {
                                                return (
                                                    <WrapperWhoosaleCard key={i} brand={el.title}>
                                                        {el.items.map((item) => {
                                                            let contextChats = [];
                                                            if (order_items_chat?.length) {
                                                                contextChats = order_items_chat.filter(message => message.item_id === item.id)
                                                            }
                                                            return (
                                                                <Card
                                                                    key={`item-${item.id}`}
                                                                    statusChat={statusChat}
                                                                    title={item.title}
                                                                    size={item.size}
                                                                    color={item.color}
                                                                    status={item.status}
                                                                    prices={item.prices}
                                                                    order={item.order}
                                                                    brand={item.brand}
                                                                    change_agreement={item.change_agreement}
                                                                    comment={[]}
                                                                    commentImage={item?.comment_image !== '-' ? item?.comment_image : null}
                                                                    image={item.image}
                                                                    id={item.id}
                                                                    url={item.product_url}
                                                                    role={role}
                                                                    can_cancel={item.can_cancel}
                                                                    currency={currency}
                                                                    redeemed={item.redeemed}
                                                                    sended={item.sended}
                                                                    contextChats={contextChats[0]}
                                                                    deleteElementOrder={deleteElementOrder}
                                                                    sendCommentFromTextField={sendCommentFromTextField}
                                                                    clickOpenCommit={clickOpenCommit}
                                                                    openModalImage={openModalImage}
                                                                    sendMessage={sendMessage}
                                                                    isLoadingCancelItems={isLoadingCancelItems}
                                                                    product_url={item.product_url}
                                                                    handleStatusChat={handleStatusChat}
                                                                    isLoaderSendMessage={isLoaderSendMessage}
                                                                    {...item}
                                                                />
                                                            );
                                                        })}
                                                    </WrapperWhoosaleCard>
                                                );
                                            })
                                        }
                                    </React.Fragment>
                                )
                    : <BlockSpinner.SpinnerWrapperSpinnerCenterMargin>
                        <BlockSpinner.Spinner sizeHeight='30' sizeWidth='30' />
                    </BlockSpinner.SpinnerWrapperSpinnerCenterMargin>
                    }
                        </LeftSideCol>
                        <RightSideCol>
                            <Chat
                                idOrder={idOrder}
                                heandlerClickInfo={heandlerClickInfo}
                                sendMessage={sendMessage}
                                order_chat={order_chat}
                                openModalImage={openModalImage}
                                openModalVideo={openModalVideo}
                                statusChat={statusChat}
                                handleStatusChat={handleStatusChat}
                                isLoaderSendMessage={isLoaderSendMessage}
                            />
                        </RightSideCol>
                    </SectionWrapper>

        </React.Fragment>
    )
}

export default (DetailsOrders);
