import React, { useState } from 'react';
import { Formik } from 'formik';
import Form from '../../Form';
import ChatFieldUser from '../ChatField/ChatFieldUser';
import ChatFieldAdmin from '../ChatField/ChatFieldAdmin';
import SendChatBlock from '../SendChatBlock/SendChatBlock';
import ChatFieldsWrapper from '../ChatField/ChatFieldsWrapper';
import WrapperChat from '../SectionWrapper/WrapperChat';
import HeadChat from '../HeadChat/HeadChat';

const Chat = ({
  idOrder,
  heandlerClickInfo,
  sendMessage,
  order_chat,
  openModalImage,
  openModalVideo,
  statusChat,
  handleStatusChat,
  isLoaderSendMessage,
}) => {

  const valuesState = {
    message: '',
    orderChat: idOrder,
    files: [],
    activeBtnMessageForProduct: true,
  }

  return (
    <WrapperChat>
      <HeadChat
        heandlerClickInfo={heandlerClickInfo}
      />

      <Formik
        enableReinitialize
        onSubmit={sendMessage}
        initialValues={valuesState}
      >
        {({ handleSubmit, values, handleChange, setFieldValue, setValues }) => {

          return (
            <Form
              onSubmit={handleSubmit}
            >
              <ChatFieldsWrapper>
                {
                  order_chat?.length ?(
                    order_chat.map((el, i) => {
                      if (el.is_me) {
                        return (
                          <ChatFieldUser
                            openModalImage={openModalImage}
                            openModalVideo={openModalVideo}
                            key={`us-${i}`}
                            id={i}
                            {...el}
                          />
                        );
                      } else {
                        return (
                          <ChatFieldAdmin
                            openModalImage={openModalImage}
                            openModalVideo={openModalVideo}
                            key={`ad-${i}`}
                            {...el}
                          />
                        );
                      }
                    })
                    ): null
                }

              </ChatFieldsWrapper>
              <SendChatBlock
                chatReady={statusChat?.chat}
                values={values}
                nameInput={'message'}
                nameFile={'files'}
                setFieldValue={setFieldValue}
                placeholder={'Написать Менеджеру по упаковке'}
                handleStatusChat={handleStatusChat}
                isLoaderSendMessage={isLoaderSendMessage}
              />
            </Form>
          );
        }}
      </Formik>
    </WrapperChat>
  );
};

export default React.memo(Chat);
